.home-container-1 {
  display: flex;
  min-height: 100vh;
  max-width: 1250px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin: 0% auto;
}
.home-container-2 {
  max-width: 1100px;
  width: calc(100% - 100px);
  border: 1px solid #d6d9dc;
  padding: 24px;
  box-sizing: border-box;
}
