.tag-h1{
    font-weight:400;
}

.tag-p{
    margin: 0px;
    font-size: 14px;
}

.tags-list-container{
    padding: 30px 0px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px,1fr));
    gap: 10px;
}

.tags-list-container .tag{
padding: 10px;
border: 1px solid #d2d2d2;
border-radius: 2px;
}

.tag h5{
    display:inline-block;
    margin: 10px 0px;
    padding: 5px 5px;
    background-color: #e1ecf4;
    color: #3d739d;
}

.tag p{
    font-size: 14px;
    color: #323232;
    line-height: 17px;
}